<template>
  <div class="QueryCompanyInfo">
    <h4>{{msg.name}}</h4>
    <a href="javascript:history.back(-1)">返回</a>
    <table class="table table-bordered table-striped table-hover table-condensed table-responsive">
      <thead>
        <tr>
          <th>公司名</th>
          <th>描述</th>
          <th>创建时间</th>
          <th>详情</th>
        </tr>
      </thead>
      <tbody>
          <tr>
            <td>{{msg.name}}</td>
            <td>{{msg.description}}</td>
            <td>{{msg.createTime}}</td>
            <td>
                <button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target=".bs-example-modal-lg" @click="id=msg.id;componentId=1;subTitle=msg.name;secdTitle='合同'">
                   合同
                </button>
                <button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target=".bs-example-modal-lg" @click="id=msg.id;componentId=2;subTitle=msg.name;secdTitle='音乐'">
                   音乐
                </button>
                <button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target=".bs-example-modal-lg" @click="id=msg.id;componentId=3;subTitle=msg.name;secdTitle='购买数据'">
                   购买数据
                </button>
            </td>
          </tr>
      </tbody>
    </table>
    <div class="modal fade bs-example-modal-lg"  tabindex="-1" role="dialog" aria-labelledby="myLargeModalLabel">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" id="myModalLabel">{{secdTitle}}</h4>
                </div>
                <div class="modal-body">
                    <query-company-agreement :id="id" :subTitle="subTitle" v-if="componentId==1"></query-company-agreement>
                    <query-company-music :id="id" :subTitle="subTitle" v-if="componentId==2"></query-company-music>
                    <query-buy-data-institutionId :id="id" :subTitle="subTitle" v-if="componentId==3"></query-buy-data-institutionId>
                </div>
                <!--<div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
                    <button type="button" class="btn btn-primary">Save changes</button>
                </div>-->
            </div>
        </div>
    </div>
  </div>
</template>

<script>
    import QueryCompanyAgreement from './QueryCompanyAgreement'
    import QueryCompanyMusic from './QueryCompanyMusic'
    import QueryBuyDataInstitutionId from './QueryBuyDataInstitutionId'
    export default {
        name: 'QueryCompanyInfo',
        props: ['title'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess'),
                institutionId: '',
                id: '',
                componentId: '',
                subTitle: '',
                secdTitle:''
            }
        },
        created: function() {
            this.getData(this.id);
            setNProgress();
        },
        methods: {
            getData: function(id) {
                var parameter = getParameter('billing.institutionInfo', this.session, {
                    'institutionId': id|this.$route.params.id
                });

                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log('查看某公司信息')
                    console.log(msg);
                    this.msg = msg.body.body.info;
                    //日期转换成常用格式
                    this.msg.createTime =formatTime(this.msg.createTime);
                },function(error){
                    console.log(error);
                })
            }
        },
        watch: {
            id: function(val, oldval) {
                this.getData(val)
            }
        },
        components: {
            'query-company-agreement': QueryCompanyAgreement,
            'query-company-music': QueryCompanyMusic,
            'query-buy-data-institutionId': QueryBuyDataInstitutionId,

        }

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h4{
        text-align:center;
        font-weight: normal;
    }
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
    
</style>


