// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'

import '../static/css/reset.css'
import '../static/libs/bootstrap/css/bootstrap.css'
import '../static/css/main.css'
import '../static/plugins/NProgress/nprogress.css'

import '../static/libs/bootstrap/js/bootstrap.js'

// datepicker插件
import '../static/plugins/Datepicker/css/foundation-datepicker.min.css'
import '../static/plugins/Datepicker/css/foundation.min.css'
import '../static/plugins/Datepicker/js/foundation-datepicker.js'
// import '../static/plugins/Datepicker/js/locales/foundation-datepicker.zh-CN.js'

// bootstrap-paginator分页插件
import '../static/plugins/bootstrap-paginator/bootstrap-paginator.js'

//md5转换插件
import '../static/plugins/jQuery.md5/jQuery.md5.js'

Vue.http.options.headers = {
    'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
};
// post的时候会把JSON对象转成formdata
Vue.http.options.emulateJSON = true;

global.URL = 'http://13.13.13.215:3001/';
/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    template: '<App/>',
    components: { App }
})


