<template>
  <div class="Count">
    <h3>{{title}}</h3>
    <br>
    <!--日期选择-->
    <div class="DateTimePickerBox">
        <div class="container">
            <table class="table">
                <thead>
                    <tr>
                        <th>开始日期:
                            <input type="text" class="span2" value="" id="AllCountStart" v-model="startTime">
                        </th>
                        <th>结束日期:
                            <input type="text" class="span2" value="" id="AllCountEnd" v-model="endTime">
                        </th>
                        <th class="queryBtn">
                         <button class="btn btn-primary btn-sm" @click="getDataBytime">查询</button>
                        </th>
                    </tr>
                </thead>
            </table>
        </div>
    </div>
    <!--表格内容-->
    <div class="panel panel-default">
        <table class="table  table-bordered table-striped table-hover table-condensed table-responsive">
        <thead>
            <tr>
                <th>序号</th>
                <th>公司名</th>
                <th>描述</th>
                <th>版权公司收入(元)</th>
                <th>find公司收入(元)</th>
                <th>总计(元)</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item,index) in msg.others">
                <td>{{index}}</td>
                <td><router-link :to="{name:'QueryCompanyInfo',params:{id:item.id}}">{{item.name}}</router-link></td>
                <td>{{item.description}}</td>
                <td>{{item.money}}</td>
                <td>{{item.findMoney}}</td>
                <td>{{item.allMoney}}</td>
            </tr>
            <tr>
                <td>合计</td>
                <td></td>
                <td></td>
                <td>{{msg.otherMoney}}</td>
                <td>{{msg.money}}</td>
                <td>{{msg.allMoney}}</td>
            </tr>
        </tbody>
        </table>
    </div>
  </div>
</template>

<script>
    export default {
        name: 'QueryAllCount',
        props: ['title'],
        data() {
            return {
                msg: 'Welcome to Your Vue.js App',
                session: localStorage.getItem('sess'),
                startTime:'',
                endTime:''

            }
        },
        created: function() {
            this.timeInit();
            this.datepickerset();

            this.getData();
            setNProgress();
        },
        // html挂在完成后删除上一个页面生成的datepicker元素
        mounted:function(){
            $('.datetimepicker').remove(); 
        },
        methods: {
            getData: function() {
                var parameter=getParameter('billing.allStatistics',this.session,{
                    'startTime':Date.parse(this.startTime),
                    'endTime':Date.parse(this.endTime)
                })

                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log('统计')
                    console.log(msg);
                    this.msg = msg.body.body;
                },function(error){
                    console.log(error);
                })
            },
            getDataBytime:function(){
                var startTime=$("#AllCountStart").val();
                var endTime=$("#AllCountEnd").val();
                this.startTime=startTime;
                this.endTime=endTime;

                // startTime=Date.parse(startTime);
                // endTime=Date.parse(endTime);

                this.getData();
            },
            timeInit:function(){
                this.startTime='';
                this.endTime='';
                // 进入页面时清除上次动态生成的元素
                 $(".datepicker").remove();
            },
            datepickerset:function(){
                // datepicker插件配置
                $(function(){
                    var nowTemp = new Date();
                    var now = new Date(nowTemp.getFullYear(), nowTemp.getMonth(), nowTemp.getDate(), 0, 0, 0, 0);
                    
                    var checkin = $('#AllCountStart').fdatepicker({
                        onRender: function(date) {
                            return date.valueOf() > now.valueOf() ? 'disabled' : '';
                        },format:'yyyy-mm-dd hh:mm:ss',pickTime: true
                    }).on('changeDate', function(ev) {
                        if (ev.date.valueOf() > checkout.date.valueOf()) {
                            var newDate = new Date(ev.date)
                            newDate.setDate(newDate.getDate() + 1);
                            checkout.update(newDate);
                        }
                        checkin.hide();
                        $('#AllCountEnd')[0].focus();
                    }).data('datepicker');
                    var checkout = $('#AllCountEnd').fdatepicker({
                        onRender: function(date) {
                            // return date.valueOf() <= checkin.date.valueOf()|date.valueOf() > now.valueOf() ? 'disabled' : '';
                            return date.valueOf() < checkin.date.valueOf()? 'disabled' : '';

                        },format:'yyyy-mm-dd hh:mm:ss',pickTime: true
                    }).on('changeDate', function(ev) {
                        checkout.hide();
                    }).data('datepicker');
                })
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h3 {
        font-weight: normal;
        text-align:center;
    }
    
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
    .DateTimePickerBox .container {
        width: 100%;
    }
    .DateTimePickerBox .container .queryBtn{
        vertical-align: middle;
    }

</style>


