<template>
  <div id="app">
      <!--主界面-->
    <div v-if="loginSess">
        <div class="wrapBox">
            <div class="header">
                <img src="../static/images/u22.png" alt="">
                <span class="title">易弹对账管理</span>
                <span class="logout" @click="logout">退出</span>
            </div>
            <div class="reconciliation">
                <div class="leftNav">
                    <ul class="navbar navbar-inverse">
                        <li v-for="item in navData" class="navItem">
                            <a :href="item.href" :class="{active:item.id==activeId}" @click="activeId = item.id;titleName=item.name;save(item.id,item.name);">
                            <img v-if="activeId==item.id" :src="item.svg+'1.svg'" alt="">                                                    
                            <img v-else :src="item.svg+'.svg'" alt="">                                                    
                                {{item.name}}
                            </a>
                        </li>
                    </ul>
                </div>
                <div class="rightContent">
                    <transition name="fade">
                        <router-view :title="titleName">

                        </router-view>
                    </transition>
                </div>
            </div>
        </div>
    </div>
    
    <!--登录界面-->
    <div v-else class="loginPage" style="background: url('./static/images/backgrounds/1.jpg')  no-repeat right top / 100% 100%">
        <div class="loginInfo">{{loginInfo}}</div>
        <div class="loginBox">
            <div class="logo">
                <img src="../static/images/u22.png" alt="">
            </div>
            <form >
                <div>
                    <input type="text" name="username" placeholder="用户名" v-model="account" required @blur="nameTest"><span>{{accountTips}}</span>
                </div>
                <div>
                    <input type="password" name="psd" placeholder="密码" v-model="password" required @blur="psdTest"><span>{{passwordTips}}</span>
                </div>
                <input type="button" class="loginBtn" value="登录" @click.enter="login">
            </form>
        </div>
    </div>
  </div>
</template>
<script>
    export default {
        name: 'app',
        data() {
            return {
                navData: [],
                activeId: sessionStorage.getItem('activeId'),
                titleName: sessionStorage.getItem('titleName'),
                loginSess: localStorage.getItem('sess'),
                account: '',
                password: '',
                isShow: true,
                accountTips: '',
                passwordTips: '',
                loginInfo: ''
            }
        },

        created: function() {
            this.getNav();
            this.bgiToggle();

            setNProgress();
        },
        methods: {
            //保存id与name，刷新后保持原位
            save: function(id, name) {
                sessionStorage.setItem('activeId', id);
                sessionStorage.setItem('titleName', name);
            },
            // 获取导航栏信息
            getNav: function() {
                var mark = localStorage.getItem('mark');

                // 导航配置信息
                var copyrightNav = [{
                    'name': '公司信息',
                    'href': '#/CompanyInfo',
                    'id': 1,
                    'svg':'./static/libs/font/svg/companyInfo'
                }, {
                    'name': '合同',
                    'href': '#/Agreement',
                    'id': 2,
                    'svg':'./static/libs/font/svg/argument'
                }, {
                    'name': '数据',
                    'href': '#/Data',
                    'id': 3,
                    'svg':'./static/libs/font/svg/data'
                }, {
                    'name': '版权音乐',
                    'href': '#/LicensedMusic',
                    'id': 4,
                    'svg':'./static/libs/font/svg/music'
                },{
                    'name':'修改密码',
                    'href':'#/changePassword',
                    'id':5,
                    'svg':'./static/libs/font/svg/password'
                    }
                ];

                var findNav = [{
                        'name': '版权方',
                        'href': '#/QueryCopyrightUser',
                        'id': 1,
                        'svg':'./static/libs/font/svg/human'
                    },
                    {
                        'name': '购买数据',
                        'href': '#/QueryBuyData',
                        'id': 2,
                        'svg':'./static/libs/font/svg/data'                        
                    }, {
                        'name': '统计',
                        'href': '#/QueryAllCount',
                        'id': 3,
                        'svg':'./static/libs/font/svg/count' 
                    },{
                        'name':'修改密码',
                        'href':'#/changePassword',
                        'id':4,
                        'svg':'./static/libs/font/svg/password' 
                    }
                ];
                console.log(mark);
                if (mark == 1) {
                    this.navData = copyrightNav;
                } else if (mark == 0) {
                    this.navData = findNav;
                }
            },
            //登录
            login: function() {
                var that = this;
                var parameter = getParameter('billing.login', '', {
                    'account': that.account,
                    'password': $.md5(that.password)
                });
                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg, textStatus, xhr){
                    console.log(msg);
                    if (msg.body.header.code == 0) {
                        console.log('登录成功');
                        console.log(msg);
                        this.loginInfo = msg.body.header.desc;
                        // 保存session
                        localStorage.setItem('sess', msg.body.body.sessionId);
                        localStorage.setItem('mark', msg.body.body.mark);
                        // 初始化
                        sessionStorage.setItem('activeId', '');
                        sessionStorage.setItem('titleName', '');

                        //完成后刷新页面
                        location.replace(document.referrer);

                    } else {
                        this.loginInfo = msg.body.header.desc;
                    }
                },function(error){

                })
            },
            logout: function() {
                localStorage.removeItem('sess');
                //改变loginFlag值，切换至主界面
                //完成后刷新页面
                location.replace(document.referrer);
            },
            // 背景图片切换
            bgiToggle: function() {
                $('.loginPage').css('background', 'url("./static/images/backgrounds/2.jpg") no-repeat right top / 100% 100%');
                var i = 2;
                setInterval(function() {
                    // debugger;
                    $('.loginPage').css('background', 'url("./static/images/backgrounds/' + i + '.jpg") no-repeat right top / 100% 100%');
                    i = i + 1;
                    if (i == 4) {
                        i = 1;
                    }
                }, 10000);

            },
            // 表单验证
            nameTest: function() {
                var reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
                console.log('用户名:'+reg.test(this.account));
                if (!reg.test(this.account)) {
                    this.accountTips = '请输入正确的邮箱格式'
                } else {
                    this.accountTips = ''
                }
            },
            psdTest: function() {
                var reg = /^\w{6,16}$/;
                console.log('密码:'+reg.test(this.password));
                if (!reg.test(this.password)) {
                    this.passwordTips = '请输入6-16位数字、字母、下划线组合'
                } else {
                    this.passwordTips = ''
                }
            }
        }
    }
</script>


<style>
    #app {
        height: 100%;
    }
    #app .wrapBox {
        height: 100%;
    }
    #app .wrapBox>.header {
        height: 61px;
        width: 100%;
        background-color: #4d4d4d;
        position: absolute;
        z-index: 1000;
        min-width:768px;
    }
    #app .reconciliation {
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
    }

    .reconciliation {
        position: absolute;
        top: 61px;
    }

    .header>img {
        position: absolute;
        left: 6px;
        top: 0px;
        width: 97px;
        height: 61px;
    }

    .header>.title {
        position: absolute;
        left: 113px;
        top: 27px;
        width: 120px;
        height: 25px;
        font-family: 'PingFangSC-Semibold', 'PingFang SC Semibold', 'PingFang SC';
        font-weight: 650;
        font-style: normal;
        color: #FFFFFF;
        font-size: 18px;
        font-weight: bold;
    }

    .header>.logout {
        position: absolute;
        right: 5%;
        top: 17px;
        width: 80px;
        height: 32px;
        font-family: 'PingFangSC-Regular', 'PingFang SC';
        font-weight: 400;
        font-style: normal;
        color: #FFFFFF;
        background-color: #d8534e;
        text-align: center;
        line-height: 32px;
        font-size: 13px;
        cursor: pointer;
        border-radius: 5px;
    }

    

    
    /*左边导航*/

    .leftNav {
        width: 15%;
        height: 100%;
        min-width:150px;
        min-height:500px;
        background-color: #202020;
        position: absolute;
        left: 0;
        box-sizing: border-box;
        padding-top: 61px;
        z-index: 10;
    }
    .leftNav ul {
        border: none;
        margin:0;
        padding:0;
    }
    .leftNav li {
        position:relative;
        background-color: #202020;
        height: 61px;
        line-height: 61px;
        text-align: center;
        border-bottom: 1px solid #3e3e3e;
    }
    .leftNav li img{
        position:absolute;
        height:20px;
        top:50%;
        left:15%;
        transform: translateY(-50%);
    }
    .leftNav li a {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 0%;
        color: #fff;
        text-align: center;
        transition: all 0.5s ease 0s;
        text-decoration: none;
    }

    .leftNav a.active {
        color: red;
        background-color: #fdfdfd;
        font-weight: 700;
    }

    .wrapBox .leftNav li:hover a {
        background-color: #fff;
        color: #000;
    }
    
    
    /*右边内容*/

    .rightContent {
        background-color: #fdfdfd;        
        /*position: absolute;*/
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding-left: 16%;
        padding-top: 81px;
        padding-right: 10px;
        background:#f0f0f0;
        min-width:768px;
        min-height:500px;
        overflow:auto;
    }
    @media screen and (max-width:1000px){
        .rightContent{ 
            padding-left: 160px;
        }
    }
    /*载入动画*/
    .fade-enter-active {
        transition: opacity .5s
    }
    
    .fade-enter {
        opacity: 0
    }

    /*表格 */
    table {
        /*width: 70%;*/
        /*border: 1px solid #000;*/
    }
    
		/*
		table>tbody>tr:hover{
			background-color:green;
		}
		
    thead>tr:nth-child(1) {
        background: -webkit-linear-gradient(top, #3c3c3c, #5f5f5f);
        color: #fff;
    }

    tbody>tr:nth-child(odd) {
        background-color: #e8dde6;
    }

    tbody>tr:nth-child(even) {
        background-color: #edf3f1;
    }
		*/
		

    /*登录界面样式·*/
    .loginPage {
        width: 100%;
        height: 100%;
        transition: all 1s ease 0s;
    }
    
    .loginInfo {
        width: 50%;
        height: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 50px;
        text-align: center;
        /*background-color: rgba(0, 0, 0, .7);*/
    }
    
    .loginBox {
        width: 100%;
        background-color: rgba(0, 0, 0, 0);
        text-align: center;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        color: #fff;
        font-size: 20px;
        border-radius: 5%;
    }
    .loginBox form div {
        position: relative;
    }
    /*表单验证提示*/
    
    .loginBox form span {
        position: absolute;
        top: 50%;
        margin-left:150px;
        font-size: 12px;
        color: red;
        font-weight: 700;
        /*transform: translateY(-50%)*/
    }
    
    .loginBox>form input {
        width: 270px;
        height: 42px;
        margin-top: 25px;
        padding: 0 15px;
        background: #2d2d2d;
        background: rgba(45, 45, 45, .15);
        -moz-border-radius: 6px;
        -webkit-border-radius: 6px;
        border-radius: 6px;
        border: 1px solid #3d3d3d;
        border: 1px solid rgba(255, 255, 255, .15);
        -webkit-box-shadow: 0 2px 3px 0 rgba(0, 0, 0, .1) inset;
        font-family: 'PT Sans', Helvetica, Arial, sans-serif;
        font-size: 14px;
        color: #fff;
        text-shadow: 0 1px 2px rgba(0, 0, 0, .1);
        -webkit-transition: all .2s;
        outline: none;
        margin:15px auto;
    }
    .loginBox>form input:focus{
        color:#000;
        background:pink;
    }
    .loginBox .loginBtn {
        box-shadow:  5px #888888;
        background: -webkit-linear-gradient(top, #ff730e, #ef4300, #ff730e);
    }
    
    .loginBox .loginBtn:hover {
        background: -webkit-linear-gradient(top, #ef4300, #ef4300, #ef4300);
    }
    
    .loginBox .loginBtn:active {
        background: #ccc;
    }
    /*placeholder样式*/
    
    input::-webkit-input-placeholder {
        /* WebKit browsers */
        color: #fff;
    }
    
    input:-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */
        color: #fff;
    }
    
    input::-moz-placeholder {
        /* Mozilla Firefox 19+ */
        color: #fff;
    }
    
    input:-ms-input-placeholder {
        /* Internet Explorer 10+ */
        color: #fff;
    }
</style>


