<template>
  <div class="QueryCompanyMusic">
    <div class="panel panel-default">
        <div class="panel-heading">{{subTitle}}</div>
        <table class="table table-bordered table-striped table-hover table-condensed table-responsive">
        <thead>
            <tr>
            <th>序号</th>
            <th>歌曲名</th>
            <th>价格</th>
            <th>折扣</th>
            <th>分成</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item,index) in msg">
                <td>{{index+1}}</td>
                <td>{{item.name}}</td>
                <td>{{item.base}}元</td>
                <td>{{item.discount!=1?item.discount*10+"折":"不打折"}}</td>
                <td>{{parseInt(item.partII*100)+"%"}}</td>
            </tr>
        </tbody>
        </table>
    </div>
    <div class="pageBox">
         <ul id="pageLimit"></ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: 'QueryCompanyMusic',
        props: ['subTitle','id'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess'),
                page: 0,
                pageNum: 1
            }
        },
        created: function() {

            this.getData(0,this.id);
            setNProgress();

        },
        methods: {
            //获取数据
            getData: function(page,id) {
                var parameter=getParameter('billing.queryMusics',this.session,{
                        'package': page,
                        'packageNum': 10,
                        'institutionId': id
                    });
                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log('查看某公司版权音乐')
                    console.log(msg);
                    this.msg = msg.body.body.data;
                    this.pageNum = Math.ceil(msg.body.body.count / 10);
                },function(error){
                    console.log(error);
                })
            },
            //根据id获取数据
            getCompanyData: function() {
                getData(0);
                $('.pageBox .page:eq(0)').addClass('active').siblings().removeClass('active');
            },
            paginator:function(){
                var that=this;
                var pagenum=this.pageNum;
                console.log("啥"+pagenum);
                $(function(){
                    $('#pageLimit').bootstrapPaginator({
                        currentPage: 1,
                        totalPages: pagenum,
                        size: "normal",
                        bootstrapMajorVersion: 3,
                        alignment: "left",
                        numberOfPages: 5,
                        itemTexts: function(type, page, current) {
                            switch (type) {
                                case "first":
                                    return "首页";
                                case "prev":
                                    return "上一页";
                                case "next":
                                    return "下一页";
                                case "last":
                                    return "末页";
                                case "page":
                                    return page;
                            }
                        },
                        onPageClicked:function(event,originalEvent,type,page){
                            console.log('hahaha'+ page);
                            that.getData(page-1,that.id);
                        }
                    });
                })
            }
            
        },
        watch:{
            id:function(val,oldval){
                this.getData(0,val);
            },pageNum:function(){
                this.paginator();
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h4{
        text-align:center;
        font-weight: normal;
    }
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
</style>


