<template>
  <div class="changePassword">
    <h1>修改密码</h1>
    <form >
        <div class="form-group">
            <label for="exampleInputPassword1">原密码：</label>
            <input type="password" class="form-control" id="exampleInputPassword1" placeholder="旧密码" v-model="oldPass" required @blur="oldPsdTest"><span>{{oldPassTips}}</span>
        </div>
        <div class="form-group">
            <label for="exampleInputPassword1">新密码：</label>
            <input type="password" class="form-control" id="exampleInputPassword2" placeholder="新密码" v-model="newPass" required @blur="newPsdTest"><span>{{newPassTips}}</span>
        </div>
        <a class="btn  btn-default" href="javascript:void(0)" @click="changePassword">修改</a>        
        <!--<button type="button" class="btn btn-lg btn-danger" data-toggle="popover" title="提示信息" :data-content="successInfo" @click="changePassword" >修改</button>-->   
    </form>
    <div id="errorInfo">
        密码不对
    </div>
  </div>
</template>

<script>
    export default {
        name:'changePassword',
        data(){
            return {
                msg:'修改密码',
                newPass:'',
                oldPass:'',
                session:localStorage.getItem('sess'),
                oldPassTips:'',
                newPassTips:'',
                successInfo:''
            }
        },
        created:function(){
            $(function () {
                $('[data-toggle="popover"]').popover()
            })
        },
        methods:{
            changePassword:function(){
                if(this.newPass==''|this.oldPass==''){
                    $('#errorInfo').text('密码不能为空');
                     $('#errorInfo').css('display','block');
                     setTimeout(function(){
                         $('#errorInfo').css('display','none');
                     },1000)
                }else if(/^\w{6,16}$/.test(this.oldPass)&/^\w{6,16}$/.test(this.newPass)){
                    var parameter = getParameter('billing.changePassword', this.session, {
                        'oldPassword':$.md5(this.oldPass),
                        'password':$.md5(this.newPass)
                    });
                    this.$http.post(URL,JSON.stringify(parameter)).then(function(response){
                        console.log(response);
                        // this.successInfo=response.body.header.desc;

                        // 设置提示信息
                        $('#errorInfo').text(response.body.header.desc);
                        $('#errorInfo').css('display','block');
                        setTimeout(function(){
                            $('#errorInfo').css('display','none');
                        },1000)
                        if(response.body.header.desc=='成功'){
                            this.newPass='';
                            this.oldPass='';
                        }
                    },function(error){

                    })
                }else{
                    $('#errorInfo').text('密码格式有误');
                    $('#errorInfo').css('display','block');
                    setTimeout(function(){
                        $('#errorInfo').css('display','none');
                    },1000)
                }
                
            },
            // 表单验证
            oldPsdTest: function() {
                var reg = /^\w{6,16}$/;
                if (!reg.test(this.oldPass)) {
                    this.oldPassTips = '请输入6-16位数字、字母、下划线组合'
                } else {
                    this.oldPassTips = '';
                }
            },
            newPsdTest: function() {
                var reg = /^\w{6,16}$/;
                if (!reg.test(this.newPass)) {
                    this.newPassTips = '请输入6-16位数字、字母、下划线组合'
                } else {
                    this.newPassTips = '';
                }
            }
        }
    }
</script>


<style scoped>
    .changePassword h1{
        text-align:center;
    }
    .changePassword{
        position:relative;
        height:100%;
        box-sizing:border-box;
        padding-top:61px;
    }
    .changePassword form{
        position:absolute;
        left:50%;
        top:50%;
        transform:translate(-50%,-50%);
    }
    .changePassword form div{

    }
    .changePassword form input{
        background:#e4e4e4;
        color:#000;
    }
    .changePassword form div {
        position: relative;
    }
    /*表单验证提示*/
    
    .changePassword form span {
        position: absolute;
        top: 105%;
        margin-left: 10px;
        font-size: 12px;
        color: red;
        font-weight: 700;
        /*transform: translateY(-50%)*/
    }
    
    .changePassword input {
        width: 270px;
        height: 42px;
        margin-bottom: 25px;
        padding: 0 15px;
        background: #2d2d2d;
        background: rgba(45, 45, 45, .15);
        -moz-border-radius: 6px;
        -webkit-border-radius: 6px;
        border-radius: 6px;
        border: 1px solid #3d3d3d;
        border: 1px solid rgba(255, 255, 255, .15);
        -moz-box-shadow: 0 2px 3px 0 rgba(0, 0, 0, .1) inset;
        -webkit-box-shadow: 0 2px 3px 0 rgba(0, 0, 0, .1) inset;
        box-shadow: 0 2px 3px 0 rgba(0, 0, 0, .1) inset;
        font-family: 'PT Sans', Helvetica, Arial, sans-serif;
        font-size: 14px;
        color: #fff;
        text-shadow: 0 1px 2px rgba(0, 0, 0, .1);
        -o-transition: all .2s;
        -moz-transition: all .2s;
        -webkit-transition: all .2s;
        -ms-transition: all .2s;
        outline: none;
        color: #fff;
    }
    #errorInfo{
        width:200px;
        height:50px;
        background:red;
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        border-radius:5px;
        text-align:center;
        line-height:50px;
        display:none;
        color:yellowgreen;
    }
</style>


