import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'

import Hello from '../components/Hello'
import ChangePassword from '../components/changePassword'
//版权方
import CompanyInfo from '../components/copyright/CompanyInfo'
import Agreement from '../components/copyright/Agreement'
import Data from '../components/copyright/Data'
import LicensedMusic from '../components/copyright/LicensedMusic'

//find公司
import QueryCopyrightUser from '../components/find/QueryCopyrightUser'
import QueryCompanyInfo from '../components/find/QueryCompanyInfo'
import QueryCompanyCount from '../components/find/QueryCompanyCount'
import QueryCompanyAgreement from '../components/find/QueryCompanyAgreement'

import QueryCompanyMusic from '../components/find/QueryCompanyMusic'
import QueryBuyData from '../components/find/QueryBuyData'
import QueryBuyDataMusicId from '../components/find/QueryBuyDataMusicId'
import QueryBuyDataUserId from '../components/find/QueryBuyDataUserId'

import QueryAllCount from '../components/find/QueryAllCount'

Vue.use(Router)
Vue.use(VueResource)

export default new Router({
    routes: [{
            path: '',
            name: 'Hello',
            component: Hello
        }, {
            path: '/ChangePassword',
            name: 'ChangePassword',
            component: ChangePassword
        },
        //版权方
        {
            path: '/CompanyInfo',
            name: 'CompanyInfo',
            component: CompanyInfo
        }, {
            path: '/Agreement',
            name: 'Agreement',
            component: Agreement
        }, {
            path: '/Data',
            name: 'Data',
            component: Data
        }, {
            path: '/LicensedMusic',
            name: 'LicensedMusic',
            component: LicensedMusic
        },
        //find公司
        {
            path: '/QueryCopyrightUser',
            name: 'QueryCopyrightUser',
            component: QueryCopyrightUser,


        }, {
            path: '/QueryCompanyInfo/:id',
            name: 'QueryCompanyInfo',
            component: QueryCompanyInfo
        }, {
            path: '/QueryCompanyMusic',
            name: 'QueryCompanyMusic',
            component: QueryCompanyMusic
        }, {
            path: '/QueryBuyData',
            name: 'QueryBuyData',
            component: QueryBuyData
        },
        {
            path: '/QueryAllCount',
            name: 'QueryAllCount',
            component: QueryAllCount
        },
        {
            path: '/QueryBuyDataMusicId:musicId',
            name: 'QueryBuyDataMusicId',
            component: QueryBuyDataMusicId
        }, {
            path: '/QueryBuyDataUserId:userId',
            name: 'QueryBuyDataUserId',
            component: QueryBuyDataUserId
        }
    ]
})


