<template>
  <div class="QueryCompanyAgreement">
    <div class="panel panel-default">
        <div class="panel-heading">{{subTitle}}</div>
        <table class="table table-bordered table-striped table-hover table-condensed table-responsive">
        <thead>
            <tr>
            <th>公司名</th>
            <th>描述</th>
            <th>打折</th>
            <th>分成</th>
            <th>创建时间</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="item in msg">
                <td>{{item.name}}</td>
                <td>{{item.description}}</td>
                <td>{{item.discount==1?"不打折":item.discount*10+"折"}}</td>
                <td>{{parseInt(item.partII*100)+"%"}}</td>
                <td>{{item.createTime}}</td>
            </tr>
        </tbody>
        </table>
    </div>
  </div>
</template>

<script>
    export default {
        name: 'QueryCompanyAgreement',
        props: ['subTitle', 'id'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess')
            }
        },
        created: function() {
            this.getData(this.id);
            setNProgress();
        },
        methods: {
            getData: function(id) {
                var parameter = getParameter('billing.queryContract', this.session, {
                    'institutionId': id
                });           
                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log('查看某公司合同')
                        console.log(msg);
                        this.msg = msg.body.body.data;
                        for (var i in this.msg) {
                            this.msg[i].createTime = formatTime(this.msg[i].createTime);
                        }
                },function(error){
                    console.log(error);
                })
            }
        },
        watch: {
            id: function(val, oldval) {
                this.getData(val)
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h4{
        text-align:center;
        font-weight: normal;
    }
    
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
</style>


