<template>
  <div class="Data">
    <h3>{{title}}</h3>
    <div class="panel panel-default">
        <div class="panel-heading">{{title}}</div>
        <table class="table table-bordered table-striped table-hover table-condensed table-responsive">
            <thead>
                <tr>
                    <th>序号</th>
                    <th>歌名</th>
                    <th>价格</th>
                    <th>折扣</th>
                    <th>分成</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in msg">
                    <td>{{index}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.base}}元</td>
                    <td>{{item.discount==1?"不打折":item.discount*10+"折"}}</td>                    
                    <td>{{parseInt(item.partII*100)+"%"}}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <div class="pageBox">
      <ul id="pageLimit"></ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: 'LicensedMusic',
        props: ['title'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess'),
                page: 0,
                pageNum: 1,
                startTime:'',
                endTime:''
            }
        },
        created: function() {
            setNProgress();
            
            this.getData(0);            
        },
        methods: {
            getData: function(p) {
                var parameter=getParameter('billing.queryMusics',this.session,{
                        'package': p,
                        'packageNum': 10
                })
                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log('查看某公司版权音乐');
                    console.log(msg);
                    this.msg = msg.body.body.data;
                    this.pageNum = Math.ceil(msg.body.body.count / 10);
                },function(error){

                })
            },
            paginator:function(){
                var that=this;
                var pagenum=this.pageNum;
                console.log(pagenum);
                $(function(){
                    $('#pageLimit').bootstrapPaginator({
                        currentPage: 1,
                        totalPages: pagenum|1,
                        size: "normal",
                        bootstrapMajorVersion: 3,
                        alignment: "left",
                        numberOfPages: 5,
                        itemTexts: function(type, page, current) {
                            switch (type) {
                                case "first":
                                    return "首页";
                                case "prev":
                                    return "上一页";
                                case "next":
                                    return "下一页";
                                case "last":
                                    return "末页";
                                case "page":
                                    return page;
                            }
                        },
                        onPageClicked:function(event,originalEvent,type,page){
                            console.log('hahaha'+ page);
                            that.getData(page-1);
                        }
                    });
                })
            }
        },
        watch:{
            pageNum:function(){
                this.paginator();
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h3 {
        font-weight: normal;
        text-align:center;
    }
    
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
    .data{
        padding-top:20px;
    }
    .DateTimePickerBox .container {
        width: 100%;
    }
    .DateTimePickerBox .container .queryBtn{
        vertical-align: middle;
    }
</style>


