<template>
  <div class="QueryCopyrightUser" >
    <h3>版权方</h3>
   <div class="panel panel-default">
        <table class="table table-bordered table-striped table-hover table-condensed table-responsive">
            <thead>
                <tr>
                <th >公司名</th>
                <th>描述</th>
                <th>创建时间</th>
                <th>详情</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in msg">
                    <td>{{item.name}}</td>
                    <td>{{item.description}}</td>
                    <td>{{item.createTime}}</td>
                    <td>
                        <button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target=".bs-example-modal-lg" @click="id=item.id;componentId=1;subTitle=item.name;secdTitle='合同'">
                        合同
                        </button>
                        <button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target=".bs-example-modal-lg" @click="id=item.id;componentId=2;subTitle=item.name;secdTitle='音乐'">
                        音乐
                        </button>
                        <button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target=".bs-example-modal-lg" @click="id=item.id;componentId=3;subTitle=item.name;secdTitle='购买数据'">
                        购买数据
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
    <!-- Modal -->
    <div class="modal fade bs-example-modal-lg"  tabindex="-1" role="dialog" aria-labelledby="myLargeModalLabel">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" id="myModalLabel">{{secdTitle}}</h4>
                </div>
                <div class="modal-body">
                    <query-company-agreement :id="id" :subTitle="subTitle" v-if="componentId==1"></query-company-agreement>
                    <query-company-music :id="id" :subTitle="subTitle" v-if="componentId==2"></query-company-music>
                    <query-buy-data-institutionId :id="id" :subTitle="subTitle" v-if="componentId==3"></query-buy-data-institutionId>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
    import QueryCompanyAgreement from './QueryCompanyAgreement'
    import QueryCompanyMusic from './QueryCompanyMusic'
    import QueryBuyDataInstitutionId from './QueryBuyDataInstitutionId'

    export default {
        name: 'QueryCopyrightUser',
        props: ['title'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess'),
                institutionId: '',
                id: '',
                componentId: '',
                subTitle: '',
                secdTitle:''
            }
        },
        created: function() {
            this.getData();
            setNProgress();
        },
        methods: {
            getData: function() {
                var that = this;
                var parameter = getParameter('billing.queryCRInstitutions', this.session, {});

                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log('查看所有版权公司')
                    console.log(msg);
                    that.msg = msg.body.body.data;
                    for (var i in that.msg) {
                        that.msg[i].createTime = formatTime(that.msg[i].createTime);
                    }
                },function(error){
                    console.log(error);
                })
            }
        },
        components: {
            'query-company-agreement': QueryCompanyAgreement,
            'query-company-music': QueryCompanyMusic,
            'query-buy-data-institutionId': QueryBuyDataInstitutionId,

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h3 {
        font-weight: normal;
        text-align:center;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
</style>


