<template>
  <div class='CompanyInfo'>
    <h4>统计</h4>
    <p>{{msg}}元</p>
    <!--<table class="table">
      <thead>
        <tr>
          <th>公司名</th>
          <th>描述</th>
          <th>创建时间</th>
          <th>最后更新时间</th>
        </tr>
      </thead>
      <tbody>
          <tr v-for="item in msg">
            <td>{{item.name}}</td>
            <td>{{item.description}}</td>
            <td>{{item.createTime}}</td>
            <td>{{item.lastUpdate}}</td>
          </tr>
      </tbody>
    </table>-->
  </div>
</template>

<script>
    export default {
        name: 'QueryCompanyCount',
        props: ['title', 'id'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess')
            }
        },
        created: function() {
            this.getData();
            setNProgress();
        },
        methods: {
            getData: function(id) {
                var parameter = getParameter('billing.statistics', this.session, {
                    'institutionId': id
                });
                var that = this;
                $.ajax({
                    type: 'post',
                    dataType: 'json',
                    url: URL,
                    async: true,
                    data: JSON.stringify(parameter),
                    success: function(msg) {
                        console.log('查看某公司统计')
                        console.log(msg);
                        that.msg = msg.body.money;
                    },
                    error: function(ex) {
                        console.log('error');
                    }
                });
            },
            getCompanyInfo: function() {
                this.$emit('getCompanyInfo', false)
            }
        },
        watch: {
            id: function(val, oldval) {
                this.getData(val)
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h4 {
        font-weight: normal;
        text-align: center;
    }
    
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
</style>


