<template>
  <div class="Data">
    <h3 v-if="id==undefined">购买数据<span>(用户编号{{userId}})</span></h3>
    <h4 v-else>购买数据</h4>
    <a href="javascript:history.back(-1)">返回</a>
        <!--日期选择-->
    <div class="DateTimePickerBox">
        <div class="container">
            <table class="table table-striped table-hover table-condensed table-responsive ">
                <thead>
                    <tr>
                        <th>开始日期:
                            <input type="text" class="span2" value="" id="BuyDataUserIdStart" v-model="startTime">
                        </th>
                        <th>结束日期:
                            <input type="text" class="span2" value="" id="BuyDataUserIdEnd" v-model="endTime">
                        </th>
                        <th class="queryBtn">
                         <button class="btn btn-primary btn-sm" @click="getDataBytime(page,id)">查询</button>
                        </th>
                    </tr>
                </thead>
            </table>
        </div>
    </div>
    <div class="panel panel-default">
        <table class="table table-bordered table-striped table-hover table-condensed table-responsive">
            <thead>
                <tr>
                <th>歌曲名</th>
                <th>用户编号</th>
                <th>音乐编号</th>
                <th>实际卖出价格(元)</th>
                <th>版权公司赚取(元)</th>
                <th>版权公司赚取占比</th>
                <th>find公司赚取(元)</th>
                <th>购买时间</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in msg">
                    <td>{{item.productName}}</td>
                    <td>{{item.userId}}</td>
                    <td>{{item.productId}}</td>
                    <td>{{item.actualMoney}}</td>
                    <td>{{item.money}}</td>
                    <td>{{parseInt(item.partII*100)+"%"}}</td>
                    <td>{{item.findMoney}}</td>
                    <td>{{item.occurTime}}</td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                    <td colspan="2">版权公司共赚取：</td>
                    <td colspan="2">{{money}}元</td>
                    <td colspan="2">find公司总共赚取：</td>
                    <td colspan="2">{{findMoney}}元</td>
                </tr>
            </tfoot>
        </table>
    </div>
    <div class="pageBox">
        <ul id="pageLimit"></ul>
        共{{pageNum}}页
    </div>
  </div>
</template>

<script>
    export default {
        name: 'QueryBuyData',
        props: ['title','id'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess'),
                page: 0,
                pageNum: 1,
                money:'',
                findMoney:'',
                userId:this.$route.params.userId,
                startTime:'',
                endTime:''
            }
        },
        created: function() {
            this.timeInit();
            this.datepickerset();

            this.getData(0,this.id);
            this.getCompanyCount(this.id);
            setNProgress();
        },
        methods: {
            //获取数据
            getData: function(page,id,userId) {
                console.log('id是'+id);
                
                var parameter=getParameter('billing.queryDataByUserId',this.session,{
                        'startTime':Date.parse(this.startTime),
                        'endTime':Date.parse(this.endTime),
                        'package': page,
                        'packageNum': 10,
                        'userId':this.userId
                });

                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                        console.log('查看购买数据');
                        console.log(msg);
                        this.msg = msg.body.body.data;
                        this.pageNum = Math.ceil(msg.body.body.count / 10);
                        for (var i in this.msg) {
                            this.msg[i].occurTime = formatTime(this.msg[i].occurTime);
                        }
                },function(error){
                    console.log(error);
                })
            },
            //获取统计数据
            getCompanyCount: function(id) {
               var parameter = getParameter('billing.statisticsByUserId', this.session, {
                    'startTime':Date.parse(this.startTime),
                    'endTime':Date.parse(this.endTime),
                    'institutionId': id,
                    'userId':this.userId                    
                });

                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log('查看某公司统计')
                    console.log(msg);
                    this.money = msg.body.body.money;
                    this.findMoney = msg.body.body.findMoney;
                                     
                },function(error){
                    console.log(error);
                })
            },
            getDataBytime:function(page,id){
                var startTime=$("#BuyDataUserIdStart").val();
                var endTime=$("#BuyDataUserIdEnd").val();
                
                this.startTime=startTime;
                this.endTime=endTime;
                
                this.getData(page,id);
                this.getCompanyCount(id);
            },
            timeInit:function(){
                // 进入页面时清除上次动态生成的元素
                 $(".datepicker").remove();
            },
            datepickerset:function(){
                $(function(){
                    var nowTemp = new Date();
                    var now = new Date(nowTemp.getFullYear(), nowTemp.getMonth(), nowTemp.getDate(), 0, 0, 0, 0);
                    
                    var checkin = $('#BuyDataUserIdStart').fdatepicker({
                        onRender: function(date) {
                            return date.valueOf() > now.valueOf() ? 'disabled' : '';
                        },format:'yyyy-mm-dd hh:mm:ss',pickTime: true
                    }).on('changeDate', function(ev) {
                        if (ev.date.valueOf() > checkout.date.valueOf()) {
                            var newDate = new Date(ev.date)
                            newDate.setDate(newDate.getDate() + 1);
                            checkout.update(newDate);
                        }
                        checkin.hide();
                        $('#BuyDataUserIdEnd')[0].focus();
                    }).data('datepicker');

                    var checkout = $('#BuyDataUserIdEnd').fdatepicker({
                        onRender: function(date) {
                            // return date.valueOf() <= checkin.date.valueOf()|date.valueOf() > now.valueOf() ? 'disabled' : '';
                            // return date.valueOf() >= checkin.date.valueOf()&date.valueOf() <= nowTemp? ' ' : 'disabled';
                            return date.valueOf() < checkin.date.valueOf()? 'disabled' : '';

                        },format:'yyyy-mm-dd hh:mm:ss',pickTime: true
                    }).on('changeDate', function(ev) {
                        checkout.hide();
                    }).data('datepicker');
                })
            },
            paginator:function(){
                var that=this;
                var pagenum=this.pageNum;
                // console.log(pagenum);
                $(function(){
                    $('#pageLimit').bootstrapPaginator({
                        currentPage: 1,
                        totalPages: pagenum,
                        size: "normal",
                        bootstrapMajorVersion: 3,
                        alignment: "left",
                        numberOfPages: 5,
                        itemTexts: function(type, page, current) {
                            switch (type) {
                                case "first":
                                    return "首页";
                                case "prev":
                                    return "上一页";
                                case "next":
                                    return "下一页";
                                case "last":
                                    return "末页";
                                case "page":
                                    return page;
                            }
                        },
                        onPageClicked:function(event,originalEvent,type,page){
                            console.log('hahaha'+ page);
                            that.getData(page-1,that.id);
                        }
                    });
                })
            }
        },
        watch:{
            id:function(val,oldval){
                this.getData(0,val);
                this.getCompanyCount(this.id);
            },pageNum:function(){
                this.paginator(); 
            }
        }
        
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h3,h4 {
        font-weight: normal;
        text-align:center;
    }
    h3>span{
        font-size:14px;
        color:#ccc;
    }
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
    .DateTimePickerBox .container {
        width: 100%;
    }
    .DateTimePickerBox .container .queryBtn{
        vertical-align: middle;
    }
</style>


