<template>
  <div class="Agreement">
    <h3>合同</h3>
    <table class="table table-bordered table-striped table-hover table-condensed table-responsive">
      <thead>
        <tr>
          <th>公司名</th>
          <th>描述</th>
          <th>折扣</th>
          <th>分成</th>
          <th>创建时间</th>
        </tr>
      </thead>
      <tbody>
          <tr v-for="item in msg">
            <td>{{item.name}}</td>
            <td>{{item.description}}</td>
            <td>{{item.discount!=1?item.discount*10+"折":"不打折"}}</td>
            <td>{{parseInt(item.partII*100)+"%"}}</td>
            <td>{{item.createTime}}</td>
          </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
    export default {
        name: 'Agreement',
        props: ['title'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess')
            }
        },
        created: function() {
            this.getData();
            setNProgress();
        },
        methods: {
            getData: function() {
                var parameter = getParameter("billing.queryContract", this.session, {});
                var that = this;
                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log("合同")
                    console.log(msg);
                    this.msg = msg.body.body.data;
                    for (var i in this.msg) {
                        this.msg[i].createTime = formatTime(this.msg[i].createTime);
                    }
                },function(error){

                })
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h3 {
        font-weight: normal;
        text-align:center;
    }
    
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
</style>


