<template>
  <div class="hello">
    <h1>{{msg}}</h1>
  </div>
</template>

<script>
    export default {
        name: 'CompanyInfo',
        data() {
            return {
                msg: '欢迎进入对账系统'
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h1,
    h2 {
        font-weight: normal;
        text-align:center;
    }
    
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
</style>


