<template>
  <div class="CompanyInfo">
    <h3>公司信息</h3>
    <table class="table table-bordered table-striped table-hover table-condensed table-responsive">
      <thead>
        <tr>
          <th>公司名</th>
          <th>描述</th>
          <th>创建时间</th>
        </tr>
      </thead>
      <tbody>
          <tr>
            <td>{{msg.name}}</td>
            <td>{{msg.description}}</td>
            <td>{{msg.createTime}}</td>
          </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
    export default {
        name: 'CompanyInfo',
        props: ['title'],
        data() {
            return {
                msg: '',
                session: localStorage.getItem('sess')
            }
        },
        created: function() {
            this.getData();
            setNProgress();
        },
        methods: {
            getData: function() {
                var parameter=getParameter('billing.institutionInfo',this.session,{});

                this.$http.post(URL,JSON.stringify(parameter)).then(function(msg){
                    console.log('公司信息')
                    console.log(msg);
                    this.msg = msg.body.body.info;
                    this.msg.createTime = formatTime(this.msg.createTime);
                },function(error){

                })
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h3 {
        font-weight: normal;
        text-align:center;
    }
    
    ul {
        list-style-type: none;
        padding: 0;
    }
    
    li {
        display: inline-block;
        margin: 0 10px;
    }
    
    a {
        color: #42b983;
    }
</style>


